.main-hero {
  padding-top: 12px;
  padding-bottom: 52px;

  @media screen and (min-width: 768px) {
    // padding-top: 23px;
  }
  @media screen and (min-width: 1444px) {
    padding-bottom: 100px;
  }
}
.main-title {
  font-family: var(--secondary-font-regular);
  font-weight: var(--main-font-weight);
  font-size: 18px;
  line-height: 1.17;
  text-align: center;
  color: var(--secondary-color-dark);

  max-width: 300px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 32px;

  @media screen and (min-width: 768px) {
    font-size: 24px;
    max-width: 350px;
    margin-bottom: 64px;
  }
  @media screen and (min-width: 1444px) {
    font-size: 48px;
    max-width: 510px;
    margin-bottom: 68px;
  }
}
.main-hero-list {
  display: flex;
  justify-content: center;
  margin-bottom: 32px;

  &__item:not(:last-child) {
    margin-right: 15px;

    @media screen and (min-width: 768px) {
      margin-right: 20px;
    }
    @media screen and (min-width: 1444px) {
      margin-right: 37px;
    }
  }

  &__walle {
    border-radius: 0px 35px 0px 0px;
  }
  &__lamps {
    border-radius: 100%;
  }
  &__phone {
    border-radius: 0px 25px;
  }

  @media screen and (min-width: 768px) {
    margin-bottom: 64px;
  }

  @media screen and (min-width: 1444px) {
    margin-bottom: 100px;
  }
}
.main-hero-text {
  font-family: var(--secondary-font-regular);
  font-weight: var(--main-font-weight);
  font-size: var(--main-text-size);
  line-height: 1.19;
  text-align: center;
  color: var(--secondary-color-dark);

  @media screen and (min-width: 768px) {
    font-size: 18px;
    line-height: 1.17;
  }
  @media screen and (min-width: 1444px) {
    font-size: 36px;
  }
}
